<template>
  <div style="overflow:auto;width:100%;padding:20px;">
    <div>
      <Button type="primary" icon="ios-plus-empty" @click="handleAddpic" style='width:120px;height:36px;'>
        添加轮播图片
      </Button>
    </div>
    <div style="min-height:600px;">
      <Table :loading="loading" border ref="swiperlist" :columns="swiperColumns" :data="swiperList"
        style='margin:20px auto'></Table>
    </div>

    <!--  <Page :total="total" show-elevator style='float:right;margin-right:20px;margin-bottom:20px;' :page-size='pagesize'></Page> -->
  </div>
</template>
<script>
  export default {
    data() {
      return {
        loading: false,
        total: 20,
         page: {
        pagesize: 10,
        pageindex: 0,
       },
        pagesize: 10,
        pageindex: 0,
        swiperList: [],
        swiperColumns: [{
          title: '#',
          width: 60,
          align: 'center',
          render: (h, params) => params.index + this.pagesize * this.pageindex + 1
        }, {
          title: '标题',
          key: 'title',
          align: 'center',

        }, {
          title: '图片',
          key: 'picurl',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', {
              attrs: {
                style: 'padding:8px;postion:relative',
              }
            }, [
              h('img', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                attrs: {
                  src: params.row.picurl,
                  style: 'width: 80px;height: 80px;border-radius: 2px;'
                },
                style: {},
              }),
            ]);
          }

        }, {
          title: '跳转详情',
          key: 'linkurl',
          align: 'center',
          width: 150,
          render(h, params) {
            let typeMap = {
              item: '商品-',
              article: '内容-',
              category: '商品分类-',
              aboutus: '公司介绍'
            };
            let text='';
            if(!!params.row.linkurl){
                let obj = JSON.parse(params.row.linkurl);
                text = typeMap[obj.type] + obj.name;
            }
            return h('div', text)
          },
        }, {
          title: '排序',
          key: 'sortvalue',
          align: 'center',
          width: 100,
        }, {
          title: '更新时间',
          key: 'updatetime',
          align: 'center',
          width: 200,
        }, {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.$router.push('/swiper/edit?id=' + params.row.id)
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.confirm('此操作将会永久删除这张轮播图片，请确认', '警告').then(() => {
                      this.delSwiperPic(params.row.id).then(res => {
                        this.$Message.success('删除成功！')
                        this.getSwiperList()
                      }).catch(erro => {

                      })
                    }).catch(erro => {
                    })
                  }
                }
              }, '删除')
            ]);
          }
        }]
      }
    },
    mounted() {
      this.$sitemap.prvname = "轮播管理";
      this.$sitemap.currname = '首页轮播';
      this.$menuactive.open = ['SwiperBlock'];
      this.$menuactive.active = 'SwiperList';
      this.getSwiperList()
    },
    methods: {
      handleAddpic() {
        this.$router.push('/swiper/add')
      },
      getSwiperList() {
        this.$http.post(this.$api.swiper_list, this.page).then(res => {
          this.swiperList = res.data.data
        }).catch(erro => {

        })
      },
      confirm(text, title) {
        let that = this
        return new Promise((resolve, reject) => {
          that.$Modal.confirm({
            title: title ? title : '确认',
            content: text,
            onOk: () => {
              return resolve(true)
            },
            onCancel: () => {
              return reject(false)
            }
          })
        })
      },
      delSwiperPic(sid) {
        return this.$http.post(this.$api.swiper_del, {sid,pagesize: 10,pageindex: 0,})
      }
    },
  }

</script>
<style scoped>
  .swiper-header {
    height: 50px;
    border-bottom: 1px solid #f0f0f0;
    font-size: 16px;
    margin-bottom: 20px;
    line-height: 50px;
    text-indent: 15px;
  }

</style>



